// NPM
import React from "react";
import PropTypes from "prop-types";
import styled from "styled-components";
import Link from "gatsby-link";
import Media from "react-media";

// COMPONENTS
import Select from "../../Form/controls/Select";

// ACTIONS/CONFIG
import { sizes } from "../../../libs/styled";
import { mainNav, languages, currencies } from "../../../data/nav";

// STYLES
const Wrap = styled.div`
  bottom: 0;
  left: 0;
  position: fixed;
  right: 0;
  top: 0;
  z-index: 20;

  select {
    color: #6e7885;
    font-size: 24px;
    margin-bottom: 15px;
    padding: 12px 0;
  }
`;

const InnerList = styled.ul`
  background: white;
  height: 100%;
  list-style-type: none;
  overflow-x: hidden;
  overflow-y: auto;
  padding: 95px 25px 25px;

  .Select,
  .Select div,
  .Select input,
  .Select span {
    font-size: 24px;
    color: #6e7885;
  }

  .Select-control {
    padding: 12px 0;
    margin-bottom: 15px;
  }

  .Select-placeholder,
  .Select--single > .Select-control .Select-value {
    padding: 12px 0;
  }

  .Select-menu-outer {
    top: 100%;
  }
`;

const NavLink = styled(Link)`
  display: block;
  font-size: 24px;
  margin-bottom: 15px;
  padding: 12px 0;

  &.is-active {
    color: #4fb798;
  }
`;

const Divider = styled.hr`
  border: none;
  margin: 15px 0;
  height: 1px;
  background: #efeff0;
`;

// MODULE
export default function MobileNav({ menuIsOpened, language, currency }) {
  if (!menuIsOpened) return null;

  return (
    <Media
      query={`(max-width: ${sizes.large})`}
      render={() => (
        <Wrap>
          <InnerList>
            <li aria-hidden="false">
              <NavLink to="/">Home</NavLink>
            </li>
            <li aria-hidden="true">
              <Divider />
            </li>
            {mainNav.map(item => (
              <li aria-hidden="false" key={item.label}>
                <NavLink activeClassName="is-active" to={item.href}>
                  {item.label}
                </NavLink>
              </li>
            ))}
            <li aria-hidden="true">
              <Divider />
            </li>
            <li>
              <Select
                onChange={val => {
                  console.log(val);
                }}
                value="eng"
                options={languages}
              />
            </li>
            <li>
              <Select
                onChange={val => {
                  console.log(val);
                }}
                value="EUR"
                options={currencies}
              />
            </li>
            <li aria-hidden="true">
              <Divider />
            </li>
            <li>
              <NavLink to="/sign-in">Sign up</NavLink>
            </li>
            <li>
              <NavLink to="/log-in">Log in</NavLink>
            </li>
          </InnerList>
        </Wrap>
      )}
    />
  );
}

// Props Validation
MobileNav.propTypes = {
  menuIsOpened: PropTypes.bool.isRequired,
  language: PropTypes.string,
  currency: PropTypes.string
};

MobileNav.defaultProps = {
  language: "english",
  currency: "EUR"
};



